#light .circles{
   background: radial-gradient(circle at 25% 15%, rgba(245, 243, 240, 0.88) 1px,rgba(239, 153, 61, 0.792) 3%, rgb(129, 191, 214) 60%, rgba(100, 51, 18, 0.708) 100%); 

  }
.circles {
	box-sizing: border-box;
 
	overflow: hidden;
